import React, { useState } from "react";

// images
import slika from "../../../sass/images/38.jpg";
import slika2 from "../../../sass/images/28.webp";
// import background from "../../../sass/images/7.jpg";
// import background2 from "../../../sass/images/3.jpg";

const About = () => {
  // stavio sam ovo ovde da bi resetovao scroll PRE NEGO STO SE COMPONENT MOUNT. Ovo je umesto componentWillMount
  const [reset] = useState(() => {
    return window.scrollTo(0, 0);
  });

  return (
    <div className="about">
      {reset}
      <div
        className="about__background"
        // style={{ backgroundImage: `url(${background})` }}>
      >
        <div className="about__mask" />
      </div>
      <div className="about__content">
        <div className="about__container">
          <h1 className="about__h1">Naa misija </h1>
          <div className="about__paragraph">
            <div className="about__paragraph--p">
              <p>
                ProEnglish predstavlja pokret i preokret u uenju engleskog
                jezika i ostvarenje je naeg sna da se sa engleskog skloni veo
                mistinosti i runa reputacija koju su stvorile godine pogrenog
                pristupa nastavi jezika u formalnom obrazovnom sistemu. Mi nismo
                obina kola niti imamo obine uionice: nai stolovi su posebno
                dizajnirani kako biste ostvarili laku komunikaciju, mi uenike
                vodimo na mesta gde mogu da vebaju engleski u realnom
                kontekstu, sa uenicima pevamo karaoke na engleskom i
                uestvujemo na projektima vanim za zajednicu.
              </p>
              <p>
                Naa misija je da engleski uinimo delom vaeg ivota, a ne
                obavezom koju imate 2, 3 puta nedeljno u odredjenim terminima.
                Mi vas uimo engleski koji neete zaboraviti jer ga uite kroz
                iskustva koja su za vas bitna. I koja su za nas bitna jer za nas
                ovo nije posao koji radimo od 9 do 5 da bismo zaradili. Engleski
                je na svestan izbor i ljubav koja tei da sve promeni na bolje.
              </p>
              <p>
                ProEnglish je centar u kome se engleskim bavimo sveobuhvatno i
                drugaije: razlaemo ono to znate poput loe izgradjene kule i
                onda zajedno sastavljamo sve zajedno na pravi nain,
                otklanjajui fosilizovane greke za koje znate da ih pravite ali
                ne znate zato niti kako da ih se reite. Gradimo zajedno
                stabilnu kuu znanja, sa vrstim temeljom, vratima, prozorima.
                Sa zidovima koji se nee osipati pod naletom vetrova zaborava.
                Dodajemo lepe prideve, nove imenice i glagole, priloge da sve
                bude lepe. A koliko emo visoko zidati zavisi od vaih
                neimarskih stremljenja: to vie spratova, to smo blie zvezdama
                i mesecu.
              </p>
            </div>
            <img src={slika2} alt="proenglish" />
            <p />
          </div>
        </div>
      </div>
      <div
        className="about__background__2"
        // style={{ backgroundImage: `url(${background2})` }}>
      >
        <div className="about__mask" />
      </div>

      <div className="about__content">
        <div className="about__container">
          <h2 className="about__h2">Nain rada </h2>
          <div className="about__paragraph">
            <img src={slika} alt="proenglish" />
            <div className="about__paragraph--p2">
              <p>
                ProEnglish predstavlja moderan pristup uenju usmeren na
                prevazilaenje blokade u govoru i usvajanje primenjivog znanja.
                Nai programi su osmiljeni tako da idu u korak sa savremenim
                metodama i tehnikama usvajanja jezika. Verujemo da je prolo
                vreme kad je uenje jezika bilo uenje gramatikih pravila
                napamet ili usvajanja novih rei prepisivanjem ili ponavljanjem.
                Jezik je iv i moan, i pravi razliku izmedju ivotinja i
                oveka. U rukama kompetentnog govornika jezik postaje alat za
                pregovaranje i osvajanje novih intelektualnih prostora. Zato mi
                jezik shvatamo ozbiljno.
              </p>
              <p>
                Mi se ne bavimo likovima iz udbenika, itanjem tekstova i
                popunjavanjem vebanja. Kod nas se ui aktivno i kroz stalnu
                konverzaciju na engleskom. Uimo vas da govorite onako kako bi
                se izrazio izvorni govornik, prirodno i bez previe razmiljanja
                o gramatikoj strukturi. Mi se bavimo vama i uimo vas engleski
                koji je potreban za vau uspenu konverzaciju, od gramatike do
                vokabulara, tako da sve moete da primenite odmah. Kod nas nema
                praznog hoda i tapkanja u mestu.
              </p>
              <p>
                Naa ciljna grupa su oni koji ele da rade fokusirano, ostvare
                merljiv napredak i spremni su da se oslobe uverenja da nikada
                nee govoriti engleski teno i tano.
              </p>

              <p>
                Ko smo mi da priamo o tome? Mi smo tim profesora sa preko 10
                godina iskustva u nastavi. Nai polaznici su na studijama na
                fakultetima od Bokonija do Oksforda, mnogi sada ive u
                inostranstvu, a uspenih poslovnih ljudi nas biraju zbog
                jedinstvnog pristupa i sposopnosti da prenesemo znanje
              </p>

              <p>
                Svi nai kursevi su konverzacijskog tipa, vrlo aktivni i
                usmereni na usvajanje primenjivog znanja i vetina za uspeno
                polaganje medjunarodnih ispita. Budui da smo upoznati sa
                psihologijom uenja, andragogijom, metodikom i metodologijom
                nastave stranih jezika, svakome prilazimo na drugaiji i
                jedinstven nain. Uveli smo i konsultacije za koje smo i sami
                nekada eleli da postoje kad smo uili predmete koji su nam
                predstavljali izazov, a one su odlina prilika da postavite
                dodatna pitanja i razreite dileme i nejasnoe bilo kad u toku
                kursa, pre i posle asa, i da dobijete smernice za rad. Uz olju
                kafe ili aja, kako nalae obiaj, u mestu koje je na drugi dom
                i u kome provodimo najvie vremena kako bismo zajedno uspeli.
                <span> Cheers!</span>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default About;
