import React, { useState } from "react";

const Course = () => {
  // stavio sam ovo ovde da bi resetovao scroll PRE NEGO STO SE COMPONENT MOUNT. Ovo je umesto componentWillMount
  const [reset] = useState(() => {
    return window.scrollTo(0, 0);
  });

  return (
    <div className="course">
      {reset}

      <div className="course__mask" />
      <div className="course__header">
        <h1 className="course__h1">Utvrdite nivo znanja</h1>
        <div className="course__content">
          <p>
            elite li brz, online test koji bi vas nakon niza gramatikih
            pitanja svrstao u odreenu grupu znanja? Po naem miljenju, jezik
            je previe kompleksan da bi se testirao u roku od 10 minuta. Zato mi
            nudimo besplatnu usmenu i pismenu proveru znanja. Smatramo da su obe
            stavke neophodne radi tane procene vaeg nivoa znanja.
          </p>

          <p>
            esto nam je znanje koja pokazujemo na papiru drugaije od onog koje
            zaista moemo da iskoristimo. Samim time se moe desiti da vam test
            koji procenjuje samo vae pasivno znanje, bez upotrebe govora,
            odredi nivo znanja koji aktivno ne moete da sustignete, ili koji
            ste, u suprotnom sluaju, prevazili i bude vam dosadan. Nakon toga
            sledi frustracija i otpor prema uenju, koji mi elimo da uklonimo i
            da uenje pretvorimo u vid aktivnosti kojoj se radujete da se
            posvetite.
          </p>

          <p>
            Zbog toga traimo da, ako ste ve uloili vreme itajui ovo,
            odvojite jo 30 minuta vaeg vremena tokom kojih ete zaista otkriti
            gde ste na skali poznavanja engleskog i ta da uinite da poboljate
            svoje znanje. Tokom ovog vremena ete proveriti vae pasivno znanje
            tokom pismenog testa, a potom i aktivno  to je pravo merilo. Na
            usmenom delu emo vas posavetovati kako da dalje uite, a vi nas
            moete pitati tom prilikom bilo ta drugo to vas interesuje. Its a
            win-winsituation!
          </p>

          <span className="course__content__span">
            Ispod ete pronai kratak opis svih nivoa po CEFR skali, kao kratak
            pregled ta se oekuje od svakog.
          </span>
        </div>
      </div>
      <div className="course__content2">
        <div className="course__mask" />
        <div className="course__lvl">
          <div className="course__text">
            <p className="course__text__p">
              Obino postoje dve vrste poetnika, pravi i lani. Pravi poetnik
              nikada ranije nije uio engleski, dok je lani poetnik uio
              nekada davno i zaboravio, ili je, to se esto deava, pokupio par
              izraza iz serija i filmova koje gleda koje bi mu pomogle u
              svakodnevnoj konverzaciji.
            </p>

            <p className="course__text__p">
              Poetnici mogu izraziti ta ele u ogranienom broju situacija (na
              ulici, u prodavnici, restoranu), mogu priati u kratkim reenicama
              o osnovnim stvarima koje su im poznate (meni u restoranu, cene,
              datumi, upoznavanje i slino). U zavisnosti od toga koliko im je
              jezik slian jeziku koje ue, toliko e moi da shvate i budu
              shvaeni. Na primer, obino su potvrdne i odrine reenice lake
              da se naue od upitnih jer je tu potrebna reenina struktura koja
              nije slina onoj u srpskom jeziku.
            </p>

            <p className="course__text__p">
              Poetnici jo uvek ne mogu da proire temu razgovora i pitaju
              spontana pitanja. Takoe teko razumeju govor na drugom jeziku
              osim ako ta osoba ne govori polako. Uglavnom pasivno sluaju i
              koriste govor tela da nadomeste re koja im nedostaje.
            </p>
            <div className="course__nav">
              <i className="fas fa-chevron-left" />
              <i className="fas fa-chevron-right" />
            </div>
          </div>
        </div>
        <div className="course__diagram__container">
          <div className="course__diagram">
            <p className="course__diagram__p">A1</p>
            <span>Pocetnik</span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Course;
