import React from "react";
import { Link } from "react-router-dom";

// img
import welcome from "../../../sass/images/welcome.png";

const HomeAbout = () => {
  return (
    <div className="home__about">
      <div className="home__about__text">
        <h2 className="home__about__h2">Ko smo mi?</h2>
        <p className="home__about__p-2">
          ProEnglish predstavlja pokret i preokret u uenju engleskog jezika i
          ostvarenje je naeg sna da se sa engleskog skloni veo mistinosti i
          runa reputacija koju su stvorile godine pogrenog pristupa nastavi
          jezika u formalnom obrazovnom sistemu. Mi nismo obina kola niti
          imamo obine uionice: nai stolovi su posebno dizajnirani kako biste
          ostvarili laku komunikaciju, mi uenike vodimo na mesta gde mogu da
          vebaju engleski u realnom kontekstu, sa uenicima pevamo karaoke na
          engleskom i uestvujemo na projektima vanim za zajednicu.{" "}
        </p>

        <p className="home__about__p-3">
          Naa misija je da engleski uinimo delom vaeg ivota, a ne obavezom
          koju imate 2, 3 puta nedeljno u odredjenim terminima. Mi vas uimo
          engleski koji neete zaboraviti jer ga uite kroz iskustva koja su za
          vas bitna. I koja su za nas bitna jer za nas ovo nije posao koji
          radimo od 9 do 5 da bismo zaradili. Engleski je na svestan izbor i
          ljubav koja tei da sve promeni na bolje.
        </p>

        <Link className="home__about__link" to="/about">
          Procitaj vise
        </Link>
      </div>
      <div className="home__about__img">
        <img src={welcome} alt="ProEnglish" />
      </div>
    </div>
  );
};

export default HomeAbout;
